
import { defineComponent } from "vue";

export default defineComponent({
  name: "BasePopup",
  props: {
    active: {
      type: Boolean,
      default: false
    },
    animation: {
      type: String,
      default: "fade",
      validator: (val: string): boolean => ["fade", "bounce"].includes(val)
    }
  },
  setup(_, { emit }) {
    function closeModal() {
      emit("close");
    }
    return {
      closeModal
    };
  }
});
