
import { defineComponent } from "vue";

export default defineComponent({
  name: "BaseInput",
  props: {
    modelValue: [String, Number],
    label: {
      type: String,
      default: "For input"
    },
    placeholder: {
      type: String,
      default: "Text..."
    },
    type: {
      type: String,
      default: "text"
    },
    errors: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    }
  }
});
