
import { defineComponent, ref } from "vue";
export default defineComponent({
  props: {
    option: {
      type: String,
      required: false,
      default: ""
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  setup() {
    const checked = ref(false);
    return {
      checked
    };
  }
});
